@font-face {
  font-family: "TT Norms Pro";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/TTNormsPro-ExtraBold.woff2"), url("../fonts/TTNormsPro-ExtraBold.woff");
  font-display: swap;
}
@font-face {
  font-family: "TT Norms Pro";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/TTNormsPro-Regular.woff2"), url("../fonts/TTNormsPro-Regular.woff");
  font-display: swap;
}
@font-face {
  font-family: "TT Norms Pro";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/TTNormsPro-Medium.woff2"), url("../fonts/TTNormsPro-Medium.woff");
  font-display: swap;
}
*,
::before,
::after {
  box-sizing: border-box;
}

body {
  width: 1100px;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  padding: 0;
  font-family: "TT Norms Pro", Arial, sans-serif;
  color: #333333;
  background: linear-gradient(to right, #f64f59, #c471ed, #12c2e9);
}

h1 {
  font-weight: 800;
  font-size: 58px;
}

.main-header {
  background: transparent;
  position: relative;
}
.main-header__wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.main-header__list {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  background-color: #2d2d44;
  list-style-type: none;
  padding: 20px;
  border-radius: 50px;
}
.main-header__link {
  font-size: 20px;
  line-height: 36px;
  font-weight: 400;
  color: white;
  text-decoration: none;
}
.main-header__text-wrapper {
  margin: 0 auto;
}
.main-header__logo {
  border-radius: 50%;
  box-shadow: 0px 0px 10px 3px #333333;
  position: absolute;
  top: 20px;
  left: 80px;
}

.main-wrapper {
  background-color: #f5f5f5;
  border-radius: 50px;
  box-shadow: 5px 5px 5px 5px blanchedalmond;
}

.main-footer {
  background: transparent;
  position: relative;
}
.main-footer__logo {
  position: absolute;
  top: 30px;
  left: 15px;
  border-radius: 50%;
  box-shadow: 0px 0px 3px 1px white;
}
.main-footer__list {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  background-color: #2d2d44;
  list-style-type: none;
  padding: 20px;
  border-radius: 50px;
}
.main-footer__item-link {
  text-decoration: none;
  color: white;
  font-size: 20px;
  line-height: 36px;
  font-weight: 400;
}

.index-main-block {
  display: flex;
  flex-direction: row;
  width: 80%;
  margin: 30px auto;
  background-color: white;
  padding: 20px;
  border-radius: 10px;
}
.index-main-block__figcaption {
  font-style: italic;
  color: gray;
  font-size: 15px;
}
.index-main-block__title {
  font-size: 36px;
  line-height: 36px;
  text-decoration: none;
}
.index-main-block__link {
  text-decoration: none;
  color: #333333;
}
.index-main-block__list {
  padding-left: 0px;
}
.index-main-block__item {
  list-style-type: none;
}
.index-main-block__info {
  font-size: 20px;
  line-height: 26px;
  color: #333333;
}
.index-main-block__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: end;
  margin-top: 60%;
}

.new-articles__wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap;
}
.new-articles__block {
  width: 500px;
  display: flex;
  flex-direction: column;
  background-color: white;
  box-shadow: 0 6px 14px rgba(0, 0, 0, 0.05);
  margin: 20px;
  border-radius: 20px;
}
.new-articles__wrapper-text {
  padding: 0px 20px 0px 20px;
}
.new-articles__title {
  font-size: 30px;
  line-height: 36px;
  margin-left: 30px;
  margin-top: 50px;
  margin-bottom: 0px;
}
.new-articles__small-title {
  font-size: 24px;
  line-height: 30px;
  margin-bottom: 0px;
}
.new-articles__info-paragraph {
  font-size: 16px;
  line-height: 22px;
  margin-top: 10px;
}
.new-articles__list {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  list-style-type: none;
  padding-left: 0;
  margin-bottom: 15px;
}
.new-articles__link {
  text-decoration: none;
  color: #333333;
}
.new-articles__info-bottom {
  margin-top: 8%;
}
.new-articles__image {
  border-radius: 20px;
}

.blog-section__wrap {
  min-height: 350px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  background-color: #f5f5f5;
  margin: 0 auto;
  width: 100%;
  margin-top: 100px;
  border-radius: 50px;
}
.blog-section__wrap-inner {
  display: flex;
  flex-direction: row;
}
.blog-section__header {
  font-size: 30px;
  line-height: 36px;
}
.blog-section__link {
  text-decoration: none;
  color: inherit;
}

.blog-card {
  background-color: white;
  max-width: 300px;
  min-height: 200px;
  margin: 10px;
}
.blog-card__info {
  font-size: 20px;
  line-height: 26px;
  text-decoration: none;
  padding: 0px 10px 0px 10px;
}
.blog-card__link {
  text-decoration: none;
}
.blog-card__text {
  font-size: 16px;
  line-height: 22px;
}

.main-article {
  background-color: white;
  border-radius: 50px;
}
.main-article__wrapper {
  width: 60%;
  margin: auto;
}
.main-article__title {
  font-weight: 600;
  font-size: 48px;
  line-height: 54px;
}
.main-article__paragraph {
  font-size: 21px;
  font-weight: 400px;
  line-height: 32px;
  font-style: normal;
  margin-top: -10px;
}
.main-article__header-two {
  font-size: 30px;
  line-height: 36px;
  margin-top: 80px;
}
.main-article__header-three {
  font-size: 24px;
  line-height: 34px;
  margin-bottom: 10px;
}
.main-article__list {
  font-size: 21px;
  line-height: 32px;
  list-style-type: circle;
}
.main-article__item {
  margin-top: 15px;
}
.main-article__list--ol {
  font-size: 21px;
  line-height: 32px;
  list-style-type: decimal;
}
.main-article__figure {
  margin-bottom: 70px;
}
.main-article__figcaption {
  font-style: italic;
  color: gray;
  font-size: 15px;
}

.article-wrapper {
  width: 700px;
  margin: 0 auto;
  font-size: 21px;
  font-weight: 400px;
  line-height: 32px;
  font-style: normal;
}
.article-wrapper__background {
  background-color: white;
}
.article-wrapper__list {
  padding-left: 0;
}
.article-wrapper__list > li {
  margin-bottom: 10px;
}
.article-wrapper__image--panda {
  display: block;
  width: 600px;
  margin: auto;
  border-radius: 10px;
}
.article-wrapper__title {
  font-style: italic;
  color: gray;
  font-size: 17px;
}

/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}
.slick-list:focus {
  outline: none;
}
.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  display: flex;
  align-items: flex-start;
  position: relative;
  left: 0;
  top: 0;
  margin-left: auto;
  margin-right: auto;
}
.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}
.slick-track:after {
  clear: both;
}
.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}
[dir=rtl] .slick-slide {
  float: right;
}
.slick-slide img {
  display: block;
}
.slick-slide.slick-loading img {
  display: none;
}
.slick-slide.dragging img {
  pointer-events: none;
}
.slick-initialized .slick-slide {
  display: block;
}
.slick-loading .slick-slide {
  visibility: hidden;
}
.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

.slider {
  margin-top: 30px;
  position: relative;
  padding: 0 100px;
}
.slider .slick-arrow {
  position: absolute;
  top: 30%;
  z-index: 10;
  font-size: 0;
  width: 100px;
  height: 100px;
  border: none;
  cursor: pointer;
}
.slider .slick-arrow.slick-prev {
  left: 0;
  background: url("/source/img/svg/arrow-left.svg") 0 0/100% no-repeat;
}
.slider .slick-arrow.slick-next {
  right: 0;
  background: url("/source/img/svg/arrow-right.svg") 0 0/100% no-repeat;
}
.slider .slick-dots {
  display: flex;
  align-items: center;
  justify-content: center;
}
.slider .slick-dots li {
  list-style-type: none;
  margin: 0 10px;
}
.slider .slick-dots .slick-active button {
  background-color: black;
  border: 1px solid #fff;
}
.slider .slick-dots button {
  font-size: 0;
  width: 20px;
  height: 20px;
  background-color: #fff;
  border-radius: 50%;
  cursor: pointer;
}
.slider .slider__image {
  width: 400px;
  height: auto;
}

.sliderbig .sliderbig__item {
  text-align: center;
}